.form__box {
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.form__label {
	flex: 1;
	color: #464646;
}

.form__input {
	flex: 3;
	padding: 0.35rem 0.35rem;
	border-radius: 6px;
	border: 1px solid #ccc;
}


.form__input:focus {
	background-color: rgba(28, 5, 132, 0.13);
}
